    const oneModel = ref<IFlatTreeItem | null>(null);
    const multiModel = ref([]);
    const nodeLoading = ref(false);

    const timeoutFn = (ms: number) => {
        return new Promise((resolve) => setTimeout(resolve, ms));
    };
    const loadNode = async (id: number) => {
        nodeLoading.value = true;
        await timeoutFn(2000);
        nodeLoading.value = false;
        const fNode = tree.value.find((f) => f.id === id);
        if (fNode) {
            tree.value.push({
                id: `${id}-${Math.random()}`,
                name: `Random node ${Math.random()}`,
                parent_id: id,
                has_children: false,
            });
        }
    };
